import React, { useState, useEffect } from "react";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';


const codeblock = {
    backgroundColor: "#404040",
    color: "white",
    height: 500,
};


const Apicall = () => {
    const [url, setUrl] = useState("");
    const [request, setRequest] = useState("");
    const [apidata, setApiData] = useState('');
    const [mode, setMode] = useState("none");
    const [response, setResponse] = useState("{}");

    useEffect(() => {

    }, [response])


    const getData = async () => {

        // GET
        if (request === "get") {
            const d = await fetch(url)
                .then(resp => resp.json())
                .then(data => setResponse(data));
        }

        // POST
        else if (request === "post") {
            const param = {
                method: 'post',
                body: apidata,
                headers: {
                    'Content-Type': 'application/json'
                }
            }
            await fetch(url, param)
                .then(resp => resp.json())
                .then(data => setResponse(data))
                .catch(error => setResponse(error));
        }

        // DELETE
        else if (request === "delete") {
            const param = {
                method: 'delete'
            }
            await fetch(url, param)
                .then(resp => resp.json())
                .then(data => setResponse(data))
                .catch(error => setResponse(error));
        }

        // PUT
        else if (request === "put") {
            const param = {
                method: 'put',
                body: apidata,
                headers: {
                    'Content-Type': 'application/json'
                }
            }
            await fetch(url, param)
                .then(resp => resp.json())
                .then(data => setResponse(data))
                .catch(error => setResponse(error));
        }
    }

    let textareastyle = {
        display: mode
    }



    return (
        <>
            <Form>
                URL: <Form.Control className="text-muted" placeholder="Url" type="url" value={url} onChange={d => setUrl(d.target.value)} required></Form.Control>

                <br />
                Request:  <label><input type="radio" onClick={() => { setMode("none"); setRequest("get"); setApiData(""); }} name="request" text="GET" />GET </label>
                <label><input type="radio" onClick={() => { setMode("block"); setRequest("post"); }} name="request" value="POST" />POST </label>
                <label><input type="radio" onClick={() => { setMode("block"); setRequest("put"); }} name="request" value="UPDATE" />UPDATE </label>
                <label><input type="radio" onClick={() => { setMode("none"); setRequest("delete"); setApiData(""); }} name="request" value="DELETE" />DELETE </label>
                <br></br>
                <Form.Control as="textarea" onChange={e => setApiData(e.target.value)} rows={5} placeholder="JSON data" style={textareastyle}></Form.Control>
                <br />
                <Button variant="outline-primary" onClick={getData}>Send</Button>
                <br />
                {response === "{}" ? "" : <div>Response : <Alert variant="dark" style={codeblock}>
                    <pre style={codeblock}>

                        {JSON.stringify(response, null, 2)}
                    </pre>
                </Alert></div>}
            </Form>
        </>
    );
};

export default Apicall;


